<template>
  <div class="container">
    <div class="file-selector">
      Select Files from Google Drive
      <p><span>Authenticate with Google Drive</span></p>
      <button type="button" @click="driveIconClicked();">
        Connect to Google Drive
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Attachment",
  data() {
    return {
      tempAttachments: [],
      attachments: [],
      pickerApiLoaded: false,
      developerKey: "AIzaSyBaQZlYTmndQYCcdlkHoVtBzpZYandwaaA",
      clientId:"7641571087-gfgbqip8dv73gh7ojqaf0e0tr3o3ot6j.apps.googleusercontent.com",
      scope: "https://www.googleapis.com/auth/drive",
      oauthToken: null
    };
  },
  components: {
  },
  mounted() {
    let gDrive = document.createElement("script");
    gDrive.setAttribute("type", "text/javascript");
    gDrive.setAttribute("src", "https://apis.google.com/js/api.js");
    document.head.appendChild(gDrive);
  },
  methods: {
    // function called on click of drive icon
   async driveIconClicked() {
      // 한번 로그하여 토큰을 받은 경우 로그인 창을 다시 열지 않도록 처리
      if( this.oauthToken === null ){
        console.log("Clicked");
        await gapi.load("auth2", () => {
          console.log("Auth2 Loaded");
          gapi.auth2.authorize(
            {
              client_id: this.clientId,
              scope: this.scope,
              immediate: false
            },
            this.handleAuthResult
          );
        });
      }
      gapi.load("picker", () => {
        console.log("Picker Loaded");
        this.pickerApiLoaded = true;
        this.createPicker();
      });
    },

    handleAuthResult(authResult) {
      console.log("Handle Auth result");
      if (authResult && !authResult.error) {
        this.oauthToken = authResult.access_token;
        this.createPicker();
      }
    },

    // Create and render a Picker object for picking user Photos.
    createPicker() {
      console.log("Create Picker");
      if (this.pickerApiLoaded && this.oauthToken) {
        var view = new google.picker.DocsView()
        view.setIncludeFolders(true)
        var picker = new google.picker.PickerBuilder()
          .enableFeature(google.picker.Feature.NAV_HIDDEN)
          .enableFeature(google.picker.Feature.MULTISELECT_ENABLED)
          .addView(view)
          .setOAuthToken(this.oauthToken)
          .setLocale('ko')
          //.setDeveloperKey(this.developerKey)
          .setCallback(this.pickerCallback)
          .build();
        picker.setVisible(true);
      }
    },
    async pickerCallback(data) {
      console.log("PickerCallback Files : ", data.docs);
      var url = "nothing";
      var name = "nothing";
      if (data[google.picker.Response.ACTION] === google.picker.Action.PICKED) {
        var doc = data[google.picker.Response.DOCUMENTS][0];
        url = doc[google.picker.Document.URL];
        name = doc.name;
        let docs = data.docs;
        var param = { fileId: doc.id, oAuthToken: this.oauthToken, name: name };
        let attachments = [];
        console.log("data",data);
        for (let i = 0; i < docs.length; i++) {
          let attachment = {};
          attachment.id = docs[i].id;
          attachment.folderId = docs[i].parentId;
          attachment.url = docs[i].url;
          attachment.link = docs[i].embedUrl;
          attachment.title = docs[i].name;
          attachment.name = docs[i].name + "." + docs[i].mimeType.split("/")[1];
          attachment.type = "gDrive";
          attachment.description = "Shared with GDrive";
          attachment.extension ="." +docs[i].mimeType.substring(docs[i].mimeType.lastIndexOf(".") + 1);
          attachment.size = docs[i].sizeBytes;
          attachment.user = JSON.parse(localStorage.getItem("user"));
          attachment.thumb = null;
          attachment.thumb_list = null;
          attachments.push(attachment);
        }
        this.tempAttachments = [...attachments];
      }
      this.download();
    },
    download(){
      var fileId = this.tempAttachments[0].id;
      var title = this.tempAttachments[0].title;

      gapi.load('client', function () {
        gapi.client.load('drive', 'v3', function () {
          var accessToken = gapi.auth.getToken().access_token;
          console.log('accessToken', accessToken);
          var request = gapi.client.drive.files.get({
            fileId: fileId,
            fields: 'name, trashed',
            alt:'media'
          }).then((resp) => {
            console.log(resp);
            var data = resp.body;
            // file download
            const url = window.URL.createObjectURL(new Blob([data]))
            const link = document.createElement('a')
            link.href = url
            link.setAttribute('download', title) //or any other extension
            document.body.appendChild(link)
            link.click()
          });
        });
      });
    }
  },
  computed: {
    getTempAttachments() {
      return this.tempAttachments;
    },
    getAttachments() {
      return this.attachments;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.file-selector {
  padding: 55px;
  font-weight: 600;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #4e5b69;
  z-index: -9;
}
figure {
  margin: 0px;
}

.dropzone-container {
  display: flex;
  flex-direction: column;
  border: 1px dashed #ccc;
  border-radius: 15px;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button {
  background: #031629;
  box-shadow: 0 0 2px 0 rgba(3, 22, 41, 0.11),
    0 6px 16px 0 rgba(3, 22, 41, 0.08);
  font-family: SFProDisplay-Regular;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 0.4px;
  padding: 12px 30px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  transition: all 0.25s;
}

button:hover {
  background-color: rgba(65, 184, 131, 1);
  border-color: transparent;
}
.separator {
  position: relative;
}
.separator:after {
  position: absolute;
  content: "";
  height: 1px;
  width: 200px;
  background: #d8d8d8;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
span {
  position: relative;
  background: #f9f9f9;
  padding: 0 4px;
  z-index: 9;
  font-size: 12px;
  color: #4e5b69;
}
</style>
